@import "./fonts";
@import "./helpers";

.portfolio {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100vh;
	background-color: $bgColor;
	.portfolio__container {
		position: relative;
		z-index: 100;
		display: flex;
		align-items: center;
		width: 90%;
		height: 90vh;
		@include md-screen {
			display: block;
		}
		// Start of Menu
		.menu {
			margin-right: 10px;
			border-radius: 5px;
			background-color: $bgInnerColor1;
			@include md-screen {
				display: none;
			}
			.menu__item {
				position: relative;
				padding: 15px 10px;
				text-align: center;
				font-size: 0.8rem;
				color: $textColor;
				cursor: pointer;
				transition: all 0.5s ease;
				flex: 1;
				&.active,
				&:hover {
					color: $mainColor;
					.portfolio-icon {
						fill: $mainColor;
					}
				}
				&:not(:last-of-type) {
					&::after {
						@extend %shadow_line;
						@include md-screen {
							left: 100%;
							width: 1px;
							height: 100%;
							background-image: linear-gradient(
								180deg,
								#fffefe08,
								#8c8c8c9a,
								#fffefe08
							);
						}
					}
				}

				.portfolio-icon {
					margin: 0 auto 10px;
					font-size: 1.3rem;
					display: block;
					transition: all 0.5s ease;
				}
			}
		}
		// End of Menu

		// Start of Intro
		.intro-card {
			position: relative;
			z-index: 10;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			width: 45%;
			height: 100%;
			border-radius: 5px;
			overflow: hidden;
			text-align: center;
			background-color: $bgInnerColor1;
			color: $textColor;
			@include md-screen {
				width: 100%;
				border-radius: 5px 5px 0 0;
			}
			.intro-card__top {
				min-height: 40%;
				margin-bottom: 25px;
				.intro-card__cover {
					height: 100%;
					background-size: cover;
					background-position: bottom;
					clip-path: polygon(0 0, 100% 0, 100% 85%, 50% 100%, 0 85%);
				}
				.intro-card__image {
					position: relative;
					width: 150px;
					height: 150px;
					margin: -100px auto 0;

					img {
						position: relative;
						width: 100%;
						height: 100%;
						border-radius: 50%;
						border: 3px solid #31313a;
						object-fit: cover;
					}

					&::before {
						content: "";
						position: absolute;
						left: -10px;
						top: 10px;
						width: 100%;
						height: 100%;
						border-radius: 50%;
						background: linear-gradient(
							90deg,
							$transparentMainColor,
							rgba(0, 0, 0, 0.164)
						);
					}
				}
			}

			.intro-card__name {
				font-size: 2rem;
				margin: 20px 0;
				@include md-screen {
					margin: 30px 0 10px 0;
					font-size: 1.5rem;
				}
			}
			.intro-card__job {
				font-size: 1.3rem;
				margin: 20px 0;
				color: $mainColor;
				@include md-screen {
					margin: 10px auto;
					font-size: 1rem;
				}
			}
			.intro-card__social {
				display: flex;
				width: 50%;
				margin: 20px auto;
				justify-content: center;
				@include md-screen {
					margin: 10px auto;
				}
				.intro-card__social-item {
					margin: 0 10px;
					.portfolio-icon {
						font-size: 1.2rem;
						cursor: pointer;
						color: $textColor;
						transition: all 0.5s ease;
						&:hover {
							color: $mainColor;
						}
					}
				}
			}
			.intro-card__cv {
				position: relative;
				width: 100%;
				padding: 20px 15px;
				background-color: transparent;
				color: $textColor;
				border: none;
				font-size: 1.3rem;
				cursor: pointer;
				transition: all 0.5s ease;
				&::before {
					@extend %shadow_line;
					top: 0;
				}
				&:hover {
					color: $mainColor;
				}
			}
		}
		// End of Intro

		.portfolio__slides {
			position: relative;
			width: 45%;
			height: 90%;
			border-radius: 0 5px 5px 0;
			overflow-y: auto;
			color: $textColor;
			@include md-screen {
				width: 100%;
				height: auto;
				border-radius: 0 0 5px 5px;
			}
			.portfolio__slide {
				position: absolute;
				left: -100%;
				top: 0;
				width: 100%;
				height: 100%;
				overflow-y: auto;
				transition: all 1s ease;
				opacity: 0;
				visibility: hidden;
				background-color: $bgInnerColor2;
				@include md-screen {
					position: relative;
					left: 0;
					opacity: 1;
					visibility: visible;
					background-color: $bgInnerColor2;
				}
				// Start of About
				&.about {
					.about__intro {
						margin-bottom: 50px;
						.about__intro-content {
							padding: 25px;
							font-size: 1.1rem;
							line-height: 30px;
							color: $textShadowColor;
						}
						.about__intro-info {
							display: flex;
							justify-content: space-between;
							flex-wrap: wrap;
							padding: 0 25px;
							@include md-screen {
								display: block;
							}
							.about__intro-item {
								width: 50%;
								@include md-screen {
									width: 100%;
									margin: 5px 0;
								}
								.colored {
									color: $textShadowColor;
								}
							}
						}
					}

					.about__languages {
						.about__languages-list {
							padding: 25px;
							.about__languages-item {
								display: flex;
								align-items: center;
								padding: 5px 0;
								.portfolio-icon {
									&:first-of-type {
										margin-right: 10px;
										color: $mainColor;
									}
									&:last-of-type {
										margin-left: 15px;
										font-size: 1.2rem;
									}
								}
							}
						}
					}
				}
				// End of About

				// Start of Resume
				&.resume {
					.resume__experience,
					.resume__education {
						padding: 25px;
						.resume__experience-header,
						.resume__education-header {
							position: relative;
							display: flex;
							align-items: center;
							padding: 15px 0;
							font-size: 1.3rem;
							color: $mainColor;
							.portfolio-icon {
								margin-right: 10px;
								font-size: 1.8rem;
							}
							&::after {
								@extend %shadow_line;
								width: 70%;
								left: 15%;
							}
						}

						.resume__experience-job,
						.resume__education-stage {
							position: relative;
							padding: 20px 0;
							.resume__experience-chip,
							.resume__education-chip {
								display: inline-block;
								padding: 3px 5px;
								margin-bottom: 10px;
								border: 1px solid;
								border-radius: 5px;
								color: $textShadowColor;
								&.present {
									color: $mainColor;
								}
							}

							.resume__experience-title,
							.resume__education-title {
								font-size: 1.1rem;
								margin-bottom: 5px;
							}

							.resume__experience-firm,
							.resume__education-degree {
								margin-bottom: 15px;
								text-decoration: underline;
								color: $textShadowColor;
							}

							.resume__experience-description,
							.resume__education-description {
								color: $textShadowColor;
							}
							&::after {
								@extend %shadow_line;
							}
						}
					}

					.resume__skills {
						.resume__skills-list {
							padding: 25px;
							.resume__skills-item {
								.portfolio-icon {
									color: $mainColor;
								}
							}
						}
					}
				}
				// End of Resume

				// Start of Works
				&.works {
					.works__list {
						padding: 25px;

						.works__list-item {
							padding: 35px 10px;
							margin-bottom: 20px;
							border: 1px solid #88888859;
							border-radius: 5px;
							text-align: center;
							.works__list-img {
								width: 300px;
								margin-bottom: 15px;
								border-radius: 5px;
								@include md-screen {
									width: 200px;
								}
							}
							.works__list-name {
								margin-bottom: 15px;
								font-size: 1.5rem;
								display: block;
							}
							.works__list-description {
								margin-bottom: 15px;
								color: $textShadowColor;
							}

							.works__list-chips {
								margin-bottom: 15px;
								display: flex;
								justify-content: center;
								flex-wrap: wrap;
								.chip {
									margin: 3px;
									padding: 2px 5px;
									border-radius: 5px;
									background-color: $transparentMainColor;
								}
							}

							.works__list-link {
								color: inherit;

								.works__list-button {
									padding: 10px 15px;
									margin: 5px 0;
									background-color: #565656;
									color: white;
									border: none;
									border-radius: 5px;
									letter-spacing: 3px;
									cursor: pointer;
								}
							}
						}
					}
				}
				// End of Works

				// Start of Contacts
				&.contacts {
					.contacts__list {
						padding: 25px;
						.contacts__list-item {
							display: flex;
							align-items: center;
							padding: 15px;
							margin-bottom: 10px;
							border: 1px solid rgba(128, 128, 128, 0.459);
							border-radius: 5px;
							overflow-wrap: anywhere;
							@include md-screen {
								font-size: 0.8rem;
							}
							.portfolio-icon {
								margin-right: 10px;
								color: $mainColor;
							}
						}
					}
				}
				// End of Contacts

				&.active {
					left: 0;
					opacity: 1;
					visibility: visible;
				}
			}
		}
	}
}
